.tabbed-form .field {
  position: relative;
  margin: 0 0 1em;
}

.tabbed-form .select2-container--default {
  width: 100% !important;
}

.tabbed-form .pum-select2 select {
  width: 100%;
}

.tabbed-form .field.cookiekey .cookie-key {
  position: relative;
  display: inline-block;
}

.tabbed-form .field.cookiekey .cookie-key button.reset {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100%;
}

.tabbed-form label {
  display: block;
  font-weight: bold;
  font-size: 1.1em;
}

.tabbed-form .field.checkbox label.pum-desc {
  display: inline;
  font-weight: inherit;
  font-size: inherit;
  margin: 0 0 1em;
}

.tabbed-form .pum-required label::after {
  color: #a00;
  content: "*";
  margin-left: 5px;
}

/* UI */
#popuptitlediv {
  position: relative;
}

#popuptitlediv #popuptitlewrap {
  border: 0;
  padding: 0;
}

#popuptitlediv #popuptitle-prompt-text {
  color: #777;
  position: absolute;
  font-size: 1.7em;
  padding: 11px 10px;
}

#popuptitlediv label {
  cursor: text;
}

#popuptitlediv #popuptitle {
  padding: 3px 8px;
  font-size: 1.7em;
  line-height: 1.125;
  height: 1.7em;
  width: 100%;
  outline: none;
  margin: 0 0 3px;
  background-color: #fff;
}

.post-type-popup #edit-slug-box {
  margin-bottom: 5px;
}

.posttypediv, .taxonomydiv {
  margin-bottom: 10px;
  clear: both;
  overflow: auto;
}

#major-publishing-actions {
  text-align: right;
}

#trigger-popmake-preview {
  padding: 5px;
}

