option.bold {
  font-weight: bold;
  font-size: 1.125em;
}

/* Range Slider Adjusters */
.popmake-range-manual {
  padding-right: 25px;
  text-align: right;
  width: 80px;
}

.range-value-unit {
  position: relative;
  display: inline-block;
  margin-left: -30px;
  margin-right: 10px;
  width: 20px;
  text-align: left;
  top: .125em;
}

/* Form Table Dividers */
.title-divider th {
  border-top: 1px solid #ccc;
  padding: 0;
}

.form-table td, .form-table tr {
  padding-top: 10px;
}

.title-divider .title {
  font-size: 1.125em;
  padding-left: 0 !important;
  padding-top: 20px !important;
  padding-bottom: 0 !important;
}

/* Upgrade Tips */
.popmake-upgrade-tip {
  color: #333;
  line-height: 2em !important;
}

div.popmake-upgrade-tip {
  margin-bottom: 15px;
  display: block;
  font-weight: bold;
}

.popmake-upgrade-tip img {
  float: left;
  margin-right: 15px;
}

/* Theme Builder */
#popmake_popup_theme_preview {
  z-index: 100;
}

#popmake_popup_theme_preview .inside {
  margin-top: 0;
  padding: 0;
  background: url(http://s.wordpress.com/mshots/v1/http://www.wordpress.org) no-repeat center top;
}

#popmake_popup_theme_preview .empreview {
  padding: 30px 20px;
  position: relative;
}

.example-popup-overlay,
.example-popup-overlay .example-popup,
.pum-overlay .title,
.pum-overlay .content,
.example-popup-overlay .close-popup,
.example-popup-overlay .close-popupclose-popup:hover,
.example-popup-overlay .close-popup:focus,
.example-popup-overlay .close-popup:active {
  background: none;
  border: none;
  bottom: auto;
  clear: none;
  cursor: default;
  /* didn't really know what the default for display should be*/
  /*display:inline;*/
  float: none;
  font-family: Arial, Helvetica, sans-serif;
  font-size: medium;
  font-style: normal;
  font-weight: normal;
  height: auto;
  left: auto;
  letter-spacing: normal;
  line-height: normal;
  max-height: none;
  max-width: none;
  min-height: 0;
  min-width: 0;
  overflow: visible;
  position: static;
  right: auto;
  text-align: left;
  text-decoration: none;
  text-indent: 0;
  text-transform: none;
  top: auto;
  visibility: visible;
  white-space: normal;
  width: auto;
  z-index: auto;
}

.example-popup, .example-popup:before, .example-popup:after,
.example-popup *, .example-popup *:before, .example-popup *:after {
  box-sizing: border-box;
}

.empreview .example-popup-overlay {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.empreview .example-popup {
  display: block;
  position: relative;
  width: 95%;
  max-width: 400px;
  margin: 0 auto;
  font-size: 16px;
  z-index: 99;
}

.empreview .example-popup .close-popup {
  text-decoration: none;
  text-align: center;
  line-height: 1;
  position: absolute;
  font-weight: bold;
  cursor: pointer;
  min-width: 1em;
}

/* Sidebar */
.popmake-support-links {
  list-style: none;
}

.popmake-support-links li {
  margin-bottom: 10px;
}

.popmake-support-links a {
  color: #20252b;
  font-family: "Montserrat";
  font-size: 1.25em;
  text-transform: uppercase;
  text-decoration: none;
}

.popmake-support-links a span {
  margin-left: 10px;
}

.popmake-support-links a img {
  top: 6px;
  position: relative;
  min-width: 24px;
  max-width: 24px;
  min-height: 24px;
  max-height: 24px;
}

.button.rounded {
  box-shadow: none;
  border: 0;
  border-radius: 2px;
  padding-left: 18px;
  padding-right: 18px;
  transition: transform .5s;
  margin-bottom: 10px;
}

.button.rounded:hover, .button.rounded:focus {
  transform: scale(1.125);
}

#popmake_popup_support {
  margin-bottom: 10px;
}

/* Welcome Page */
.popmake-badge {
  padding-top: 150px;
  height: 52px;
  width: 185px;
  color: #666;
  font-weight: bold;
  font-size: 14px;
  text-align: center;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.8);
  margin: 0 -5px;
  background: url("../images/welcome/logo.png") no-repeat center center;
}

.about-wrap .popmake-badge {
  position: absolute;
  top: 0;
  right: 0;
}

.popmake-welcome-screenshots {
  float: right;
  margin-left: 10px !important;
  padding: 0 7.5em;
}

.popmake-wrap .feature-section {
  margin-top: 20px;
}

.tab-box {
  margin-top: 40px;
  position: relative;
  border: 1px solid #ccc;
  max-width: 500px;
  padding: 20px 10px 5px;
}

.tab-box h4 {
  font: 13px/30px "Open Sans", sans-serif;
  font-weight: bold;
  position: absolute;
  background: #ccc;
  display: inline-block;
  padding: 0 10px;
  top: -48px;
  left: -1px;
}

.tab-box a {
  font: 300 18px/1.125 "Oswald";
  color: #9ABA27;
}

.tab-box button {
  font: 300 18px/1.125 "Oswald";
  color: #fff;
  background: #9ABA27;
  box-shadow: none;
  border: 0;
  padding: 5px 10px 7px;
  cursor: pointer;
  transition: transform .5s;
}

.tab-box button:hover {
  transform: scale(1.125);
}

#popmake-preview {
  position: fixed;
  z-index: 9999999 !important;
}

#popmake-overlay {
  z-index: 9999998 !important;
